<template>
  <div class="scorll-wrapper" >
    <div class="head">
      <div class="left">
        <span class="title">{{title}}</span>
        <span class="desc">{{desc}}</span>
        <ul v-show="newSongData.lanlist" class="sort">
          <li v-for="(item,index) in newSongData.lanlist" :key="item.tjreport" :class="index==Math.floor(start/10)?'active':''" @click="switchType(index)">{{item.lan}}</li>
        </ul>
      </div>
     
      <div class="right" @click="goto">
        <span>更多</span>
        <i class="iconfont icon-youjiantou"></i>
      </div>

    </div>
    <div class="list">
       <div class="iconleft" @click="reduce">
        <i class="iconfont icon-zuojiantou"></i>
      </div>
      <div class="item" v-for="item in resList" :key="item.id" @click="gotoDeatil(item.album.mid)">
        <div class="img">
          <Pic :picUrl="'https://y.qq.com/music/photo_new/T002R300x300M000'+item.album.mid+'.jpg?max_age=2592000'" :listenCount="item.action.icons" />
        </div>
        <div class="tag">
          <p>{{item.name}}</p>
          <p>{{item.singer[0].name}}</p>
          <p>{{item.album.time_public}}</p>
        </div>
        
        
      </div>
      
      <div class="iconright" @click="add">
        <i class="iconfont icon-youjiantou"></i>
      </div>
    </div>
  </div>
</template>

<script>
import {  reactive, toRefs } from 'vue'
import Pic from './Pic.vue'
import { useRouter } from 'vue-router';
export default {
  props:{
    newSongData:Object,
    title:String
  },
   components:{
    Pic
  },
  setup(props){
    console.log(props.lanlist);
    const state = reactive({
      start:0,
      end:4,
      lanlist:[]
    })
    const switchType = (e)=>{
      state.start =e*10
      state.end = state.start+4
    }
    const router = useRouter()
    const goto = ()=>{
      router.push({path:'/rankDetail',query:{id:4}})
    }
    const gotoDeatil = (id)=>{
      router.push({path:'/cdListPage',query:{id:id}})
  
    }
    const reduce = ()=>{
      if(state.start>0){
        state.start--
        state.end--
      }
      
    }
    const add = ()=>{
      if(state.end<props.newSongData.songlist.length)
      state.start++
      state.end++
    }
    return {
      reduce,
      add,
      goto,
      switchType,
      gotoDeatil,
      ...toRefs(state)
    }
  },
  computed:{
    resList(){
      // console.log('我是resList',this.newSongData.songlist);
      
    
      if(this.newSongData.songlist){
        // for(let i=0;i<this.newSongData.songlist.length;i++){
        // this.newSongData.songlist[i].cover = 'https://y.qq.com/music/photo_new/T002R300x300M000'+this.newSongData.songlist[i].mid+'_1.jpg?max_age=2592000'
        // }
        return this.newSongData.songlist.slice(this.start,this.end)
      }
      
      else{
        return []
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .scorll-wrapper{
    
    .head{
      display: flex;
      justify-content: space-between;
      cursor: pointer;
      .left{
        display: flex;
        .title{
          font-size: 25px;
          vertical-align: middle;
        }
        .desc{
          margin-left: 15px;
          font-size: 14px;
          vertical-align: middle;
          color: #969799;
        }
        .sort{
          display: flex;
          .active{
            color: #1ECD9B;
          }
          li{
            margin-left: 25px;
            line-height: 33px;
            height: 33px;
            cursor: pointer;
          }
          li:hover{
            color: #1ECD9B;
          }
          
        }
      }
    }
    .list{
      display: flex;
      margin-left: -23px;
      position: relative;
      margin-top: 20px;
      .iconleft,.iconright{
        cursor: pointer;
        position: absolute;
        display: none;
        i{
          font-size: 65px;
          font-weight: lighter; 
          color: #1ECD9B;
        }
        i:hover{
          color: #1ECD9B;
        }

      }
      .iconleft{
        left: -28px;
        top: 70px;
      }
      .iconright{
        right: -40px;
        top: 70px
      }
      .item{
        margin-left: 26px;
        width: 23%;
        cursor: pointer;
        .img{
          position: relative;
          width: 200px;
          height: 200px;
          overflow: hidden;
          .icon-bofang{
            font-size: 65px;
            position: absolute;
            font-size: 65px;
            left: 65px;
            top: 69px;
            display: none;
          }
          img{
            height: 100%;
            width: 100%;
          }
        }
        .img:hover{
          transform: translateY(-10px);
          transition: all.3s;
          img{
            filter: brightness(0.5);
            transform: scale(1.2);
            transition: all .5s;
          }
          .icon-bofang{
            display: block;
          }
        }
        .tag{
          margin-top: 10px;
        }
        .tag:hover{
          color: #1ECD9B;
        }
      }
      
    }
    .list:hover .iconleft, .list:hover .iconright{
      display: block;
    }
  }
</style>