<template>
  <div class="selected">
    <mySwipe :swipe="swipe" class="swipe" />
    <ScrollList
      class="scrollList"
      :list="recomPlaylist"
      title="官方歌单"
      desc="官方甄选订阅歌单"
    />
    <NewSongList
      :newSongData="newSongData"
      v-if="newSongData"
      title="新歌首发"
      class="newSong"
    />
    <RecommendList />
    <div class="rank-wrapper">
      <div class="head">
        <span class="title">排行榜</span>
        <span class="more" @click="goto('/home/rank')">更多<i class="iconfont icon-youjiantou"></i></span>
        
      </div>
      <div class="container">
        <div class="hot" v-for="(item,index) in topList" :key="item.groupId">
          <p class="p1">{{item.title}}</p>
          <p class="p2">新歌</p>
          <van-icon name="play-circle-o" class="bofang" @click="goto('/rankDetail',rankRouter[index])"/>
          <div class="rank">
            <div class="rank-item" v-for="(list,index) in item.song" :key="list.recType">
              <p class="songname">{{index+1}} {{list.title}}</p>
              <p>&nbsp;&nbsp;&nbsp;{{list.singerName}}</p>
            </div>
           
          </div>
        </div>
         
      </div>
    </div>
  </div>
</template>

<script>
import mySwipe from "@/components/mySwipe";
import RecommendList from "@/components/RecommendList";
import axios from "axios";
import { onMounted, reactive, toRefs } from "vue";
import ScrollList from "../components/scrollList.vue";
import NewSongList from "../components/NewSongList";
import { useRouter } from 'vue-router';
export default {
  components: {
    mySwipe,
    ScrollList,
    NewSongList,
    RecommendList
  },
  setup() {
    const state = reactive({
      swipe: [],
      recomPlaylist: [],
      newSongData: [],
      topList:[],
      rankRouter:[62,26,27,4]
    });
    const router = useRouter()
    const goto = (e,id)=>{
      router.push({path:e,query:{id:id}})
    }
    onMounted(() => {
      axios({
        url: `/api/getRecommend`,
        withCredentials: true, //关键
      }).then((res) => {
        // console.log(res);
        state.swipe = res.data.response.focus.data.content;
        state.recomPlaylist = res.data.response.recomPlaylist.data.v_hot;
        state.newSongData = res.data.response.new_song.data;
        state.topList = res.data.response.toplist.data.group[0].toplist.slice(0,4)
        // console.log(state.newSongData);
      });
    });
    return {
      ...toRefs(state),
      goto
    };
  },
};
</script>

<style scoped>
.selected {
  margin-top: 35px;
}
.swipe{
  margin-left: 33px;
}
.scrollList {
  margin-top: 25px;
}
.newSong {
  margin-top: 30px;
  margin-bottom: 30px;
}
.rank-wrapper{
  background: #F3F3F3;
  padding-top:30px ;
}
.head {
  display: flex;
  margin-top: 40px;
}
.title {
  margin-left: 412px;
  font-size: 45px;
}
.more{
  cursor: pointer;
  height: 60px;
  line-height: 60px;
  margin-left: 337px;
}
.container{
  display: flex;
  height: 850px;
  padding: 20px 74px 0 40px;
}

.hot{
  margin-top: 30px;
  margin-left: 20px;
  color: #fff;
  padding-top: 30px;
  text-align: center;
  position: relative;
  width: 200px;
  height: 600px;
  background-color: #D87B96;
  /* background-image: url(https://ts1.cn.mm.bing.net/th?id=OIP-C.hUYPsexyV9VQHwcgYCn0pQHaFG&w=176&h=170&c=8&rs=1&qlt=90&o=6&dpr=1.5&pid=3.1&rm=2);
  background-repeat: no-repeat;
  background-size: 199px 572px; */
}
.hot:hover .bofang{
  visibility: visible;
}
.hot:hover{
  transform: scale(1);
  transition: all .75s;
}
.p1{
  font-size: 22px;
}
.p2{
  margin-top: -24px;
  font-size: 30px;
}
.rank{
  text-align: start;
  padding-left: 26px;
}
.rank-item{
  margin-top: 40px;
}
.songname{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.bofang{
  font-size: 30px;
  /* background-color: #fff;  */
  border-radius: 50%;
  visibility: hidden;
  cursor: pointer;
}
</style>