<template>
  <div class="swipe-wrap">
    <div class="swipe">
      <ul class="viewWrapper">
        <li class="item" :style="transStyle" v-for="item in swipe" :key="item" @click="jumpTo(item.jump_info.url)">
          <img :src="item.pic_info.url" alt="" />
        </li>
        <li class="item" :style="transStyle" v-for="item in swipe" :key="item" @click="jumpTo(item.jump_info.url)">
          <img :src="item.pic_info.url" alt="" />
        </li>
        <ul class="point-wrapper" :style="circleStyle">
          <li
            v-for="(item, index) in swipe.length"
            :key="item"
            class="circle"
            :class="index == currentIndex ? 'active' : ''"
            @click="onHandle(index)"
          ></li>
        </ul>
      </ul>
      <van-icon name="arrow-left" class="icon-left" @click="leftMove" />
      <van-icon name="arrow" class="icon-right" @click="rightMove" />
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from 'vue-router';
export default {
  props: {
    swipe: {
      type: Array,
      default() {
        return [1, 2, 3, 4];
      },
    },
  },
  setup(props) {
    const state = reactive({
      currentIndex: 0, //滚动变量
      autoTime: 2000, //自动播放间隔时长
      wrapperWidth: 860, //整个父容器宽度
      itemWidth: 430, //子容器宽度+外边距
    });
    const onHandle = (e) => {
      state.currentIndex = e;
    };
    const leftMove = () => {
      if (state.currentIndex == 0) {
        state.currentIndex = props.swipe.length - 1;
        return;
      }
      state.currentIndex--;
    };
    const router = useRouter()
    const jumpTo = (id)=>{
      router.push({path:'/cdListPage',query:{id:id}})
    }
    const rightMove = () => {
      if (state.currentIndex >= props.swipe.length) return;
      state.currentIndex++;
    };
    const autoMove = () => {
      setInterval(() => {
        state.currentIndex++;
        if (state.currentIndex >= props.swipe.length) {
          state.currentIndex = 0;
        }
      }, state.autoTime);
    };
    onMounted(() => {
      autoMove();
    });
    return {
      autoMove,
      onHandle,
      rightMove,
      leftMove,
      jumpTo,
      ...toRefs(state),
    };
  },
  computed: {
    transStyle() {
      return `transform:translateX(${-this.currentIndex * this.itemWidth}px)`;
    },
    circleStyle(){
      return `left:calc(50% - ${this.swipe.length*30/2}px - 5px)`
    }
  },
};
</script>

<style lang="less" scoped>
.swipe-wrap {
  position: relative;
  .swipe {
    width: 980px;
    .icon-left {
      display: none;
      position: absolute;
      font-size: 50px;
      top: 31%;
      left: -33px;
      z-index: 1000;
    }
    .icon-right {
      display: none;
      position: absolute;
      font-size: 50px;
      top: 31%;
      right: 15px;
    }
    i:hover {
      display: block;
      color: aquamarine;
    }
    .viewWrapper {
      display: flex;
      position: relative;
      width: 860px;
      overflow: hidden;
      .item {
        cursor: pointer;
        margin: 30px 15px;
        width: 400px;
        height: 200px;
        flex-shrink: 0;
        img {
          width: 100%;
          border-radius: 10px;
        }
      }
      .point-wrapper {
        position: absolute;
        bottom: 12%;
        display: flex;
        .circle {
          width: 10px;
          height: 10px;
          border-radius: 50%;
          margin-left: 20px;
          background-color: #969799;
        }
        .active {
          background-color: #111;
        }
        .circle:hover {
          background-color: #07c1608c;
        }
      }
    }
  }
  .swipe:hover {
    i {
      display: block;
    }
  }
}
</style>