<template>
  <div class="scorll-wrapper">
    <div class="head">
      <div class="left">
        <span class="title">{{ title }}</span>
        <span class="desc">{{ desc }}</span>
      </div>
      <div class="right" @click="goto()">
        <span>更多</span>
        <i class="iconfont icon-youjiantou"></i>
      </div>
    </div>
    <div class="list">
      <div class="iconleft" @click="reduce">
        <i class="iconfont icon-zuojiantou"></i>
      </div>
      <div
        class="item"
        v-for="item in resList"
        :key="item.id"
        @click="gotoDeatil(item.content_id)"
      >
        <div class="img">
          <Pic :picUrl="item.cover" :listenCount="item.listen_num" />
          <!-- <i class="iconfont icon-bofang"></i> -->
        </div>
        <div class="tag">
          <span>{{ item.title }}</span>
        </div>
      </div>

      <div class="iconright" @click="add">
        <i class="iconfont icon-youjiantou"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import Pic from "./Pic.vue";
export default {
  props: {
    list: Array,
    title: String,
    desc: String,
  },
  components: {
    Pic,
  },
  setup(props) {
    const state = reactive({
      start: 0,
      end: 4,
    });
    const reduce = () => {
      if (state.start > 0) {
        state.start--;
        state.end--;
      }
    };
    const add = () => {
      if (state.end < props.list.length) state.start++;
      state.end++;
    };
    const goto = ()=>{
      router.push({path:'category'})
    }
    const router = useRouter();
    const gotoDeatil = (id) => {
      router.push({ path: "/playList", query: { id: id } });
    };
    return {
      reduce,
      add,
      goto,
      gotoDeatil,
      ...toRefs(state),
    };
  },
  computed: {
    resList() {
      return this.list.slice(this.start, this.end);
    },
  },
};
</script>

<style lang="less" scoped>
.scorll-wrapper {
  .head {
    display: flex;
    justify-content: space-between;
    span {
      cursor: pointer;
    }
    .left {
      .title {
        font-size: 25px;
        vertical-align: middle;
      }
      .desc {
        margin-left: 15px;
        font-size: 14px;
        vertical-align: middle;
        color: #969799;
      }
    }
  }
  .list {
    display: flex;
    margin-left: -23px;
    position: relative;
    margin-top: 20px;
    .iconleft,
    .iconright {
      cursor: pointer;
      position: absolute;
      display: none;
      i {
        font-size: 65px;
        font-weight: lighter;
        color: #1ecd9b;
      }
      i:hover {
        color: #1ecd9b;
      }
    }
    .iconleft {
      left: -28px;
      top: 82px;
    }
    .iconright {
      right: -40px;
      top: 85px;
    }
    .item {
      width: 23%;
      margin-left: 26px;
      cursor: pointer;
      .img {
        position: relative;
        margin-top: 15px;
        box-sizing: border-box;
        width: 200px;
        height: 200px;
        overflow: hidden;
        .icon-bofang {
          font-size: 65px;
          position: absolute;
          font-size: 65px;
          left: 65px;
          top: 69px;
          display: none;
        }
        img {
          height: 100%;
          width: 100%;
        }
      }
      .img:hover {
        transform: translateY(-10px);
        transition: all .3s;
        img {
          
          // transform: scale(1.2);
          transition: all 0.5s;
        }
        .icon-bofang {
          display: block;
        }
      }
      .tag {
        margin-top: 10px;
      }
      .tag:hover {
        color: #1ecd9b;
      }
    }

    // .item:not(:first-child) {
    //   margin-left: 26px;
    // }
  }
  .list:hover .iconleft,
  .list:hover .iconright {
    display: block;
  }
}
</style>